.loader {
  border: 1.6rem solid #f5f5f5; /* Light grey */
  border-top: 1.6rem solid orange; /* Blue */
  border-radius: 50%;
  width: 12rem;
  height: 12rem;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
