.dashboard {
  grid-area: aside;
  max-width: 20vw;
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #fff;
  color: #666;
  padding: 4rem 2rem;
  &__title {
    margin-bottom: 2rem;
    display: flex;
    align-items: center;
    font-size: 1.6rem;
    span {
      margin-left: 0.3rem;
    }
  }
}
.logo {
  margin-bottom: 2rem;
  &__name {
    color: #666;
    font-size: 2rem;
    font-weight: 700;
  }
}
li {
  text-transform: capitalize;
  font-size: 1.6rem;
  transition: color 0.3s ease-in;
  span {
    margin-left: 0.3rem;
  }
  &:hover {
    cursor: pointer;
  }
  &:not(:last-child) {
    margin-bottom: 1rem;
  }
}
.active {
  color: orange;
}
