.main {
  grid-area: main;
  td,
  th {
    font-size: 1.4rem;
    padding: 2rem !important;
  }
}
.main_title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #555;
  margin-bottom: 2rem;
}
.accept,
.reject {
  font-size: 1.6rem;
  &:hover {
    cursor: pointer;
  }
}
.accept {
  color: #24ad5b;
  margin-right: 1rem;
}
.reject {
  color: #da1628;
  margin-right: 1rem;
}
.file {
  color: #666;
  font-size: 1.6rem;
  &:hover {
    cursor: pointer;
  }
}
