.navbar__custom {
  display: flex;
  justify-content: space-between;
  align-items: center !important;
  background-color: transparent !important;
  width: 100%;
}
.custom {
  position: relative;
  max-width: 40vw;
  width: 100%;
  &__input {
    width: 100%;
    min-height: 4rem;
    font-size: 1.6rem !important;
  }
}

.search__icon {
  position: absolute;
  top: 1.25rem;
  right: 1rem;
  color: orange;
  font-size: 1.5rem;
  &:hover {
    cursor: pointer;
  }
}
.avatar {
  display: flex;
  align-items: center;
  &__name {
    font-size: 1.6rem;
    font-weight: 300;
    margin-right: 1rem;
    color: #666;
  }
  &__img {
    width: 64px;
    height: 64px;
    object-fit: cover;
    border-radius: 50%;
    border: 4px solid #fff;
    transition: border 0.3s ease-in;
    &:hover {
      border-color: orange;
      cursor: pointer;
    }
  }
  &__notification {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #666;
    font-size: 1.6rem;
    margin-left: 1rem;
    transition: color 0.3s ease-in;
    &:hover {
      cursor: pointer;
      color: orange;
    }
  }
}
